.point {
  display: inline-block;

  position: absolute;
  top: -27px;
  left: 0px;

  width: 15px;
  height: 15px;

  touch-action: none;
}

.labelForColorType {
  display: inline-block;
  position: relative;

  width: 20px;
  height: 20px;

  border-width: 0.35em;
  border-style: solid;
  border-radius: 50%;
  border-color: #902020;
  border-bottom-right-radius: 0%;

  transform: rotate(45deg);

  cursor: pointer;
  overflow: hidden;

}

.pickColor {
  position: absolute;

  margin-top: -6px;
  margin-left: 8px;

  width: 0px;
  height: 0px;

  overflow: hidden;

  z-index: 20;
}

.circle {
  width: 0px;
  margin-left: 2px;
  margin-top: 14px;

  border-width: 8px;
  border-bottom-width: 15px;
  border-style: solid;
  border-color: transparent;
  border-bottom-color: #ffffff;
  border-radius: 3px;

  background-color: transparent;

  cursor: pointer;
  touch-action: none;
}